
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Swipeout from 'vux/src/components/swipeout/swipeout.vue'
import SwipeoutItem from 'vux/src/components/swipeout/swipeout-item.vue'
import SwipeoutButton from 'vux/src/components/swipeout/swipeout-button.vue'
import Toast from 'vux/src/components/toast/index.vue'

import tools from '../../lib/tools.js';

export default {
name:'equipment',
components:{Flexbox, FlexboxItem,XButton,Swipeout, SwipeoutItem, SwipeoutButton,Toast},
props:['netType'],
data(){
return {
id:'',
showToast:false,
toastType:'text',
toastMsg:'',
equipList:[]
}

},
created(){
this.loading();
},
methods:{
loading(){
var vm = this;
let id = this.id = localStorage.getItem('md') || tools.getRequestParam('md') || tools.getCookie('user_id');
let managerId = localStorage.getItem('md') || tools.getCookie('user_id');
if(!managerId || managerId == 'undefined'|| managerId == 'null' ){
sessionStorage.setItem('tempURL',window.location.href);
tools.next('/m/login.html?tempURL='+window.location.href);
return false;
}
this.$http.axios({method:'get',url:'/deviceBinding/getDeviceBindingList?managerId='+id}).then((result) => {
let data = result.data;
if(data.successful && (data.resultCode.code=='SUCCESS')){
if(data.data){
if(data.data.length <= 0){
vm.showToast = true;
vm.toastMsg = '暂无数据';
return false;
}
vm.equipList = data.data;
}
}
});
document.body.style.height = '100%';
document.querySelector('html').style.height = '100%';

var appId = '';
var timestamp = 0;
var nonceStr = '';
var signature = '';

this.$http.post('/api/WeiXin/jssdkconfig.action',{url: location.href.split('#')[0]}).then((result) => {
let data = result.data;
wx.config({
beta:true,//开启内测接口调用，注入wx.invoke方法
debug: false, // 开启调试模式,调用的所有api的返回值会在客户端alert出来，若要查看传入的参数，可以在pc端打开，参数信息会通过log打出，仅在pc端时才会打印。
appId: data.appId, // 必填，公众号的唯一标识
timestamp: data.timeStamp, // 必填，生成签名的时间戳
nonceStr: data.nonceStr, // 必填，生成签名的随机串
signature: data.signature,// 必填，签名，见附录1
jsApiList: [
'scanQRCode',
'configWXDeviceWiFi',
'openWXDeviceLib',
'closeWXDeviceLib',
'getWXDeviceInfos',
'getWXDeviceBindTicket',
'getWXDeviceUnbindTicket',
'startScanWXDevice',
'stopScanWXDevice',
'connectWXDevice',
'disconnectWXDevice',
'sendDataToWXDevice',
'onWXDeviceBindStateChange',
'onWXDeviceStateChange',
'onScanWXDeviceResult',
'onReceiveDataFromWXDevice',
'onWXDeviceBluetoothStateChange',
], // 必填，需要使用的JS接口列表，所有JS接口列表见附录2
});
});
},
wxScan(){
let deviceCode = null;
let _this = this;
wx.scanQRCode({
needResult: 1,// 默认为0，扫描结果由微信处理，1则直接返回扫描结果，
scanType: ["qrCode","barCode"],// 可以指定扫二维码还是一维码，默认二者都有
success: function (res) {
console.log(res.resultStr);
deviceCode = res.resultStr.replace(/\/scan\//,'');
let managerId = localStorage.getItem('md') || tools.getCookie('user_id');
_this.$http.post('/deviceBinding/deviceBindingUser',{managerId:managerId,deviceCode:deviceCode}).then((result) => {
let data = result.data;
if(data.successful && (data.resultCode.code=='SUCCESS')){
_this.showToast = true;
_this.toastMsg = '设备添加成功';
setTimeout(function(){
window.location.reload(true);
},1500)
}else{
_this.showToast = true;
_this.toastMsg = data.resultCode.message;
}
});
}
});
},
wxScanWifi(){
let _this = this;
wx.invoke('configWXDeviceWiFi', {}, function(res){
var err_msg = res.err_msg;
if(err_msg == 'configWXDeviceWiFi:ok') {
_this.showToast = true;
_this.toastMsg = '设备关联成功';
let userId = localStorage.getItem('md') || tools.getCookie('user_id');
let data = {managerId:userId,deviceId:deviceId,type:type,deviceNetType:'T'};
_this.$http.post('/deviceBinding/changeNetworking',data).then((result) => {
let data = result.data;
if(data.successful && (data.resultCode.code=='SUCCESS')){
window.location.reload(true);
}
});
}
});
},
wxScanLanya(){
let _this = this;

















































































































},
bindFun(deviceId,type,netWorking){
let userId = localStorage.getItem('md') || tools.getCookie('user_id');
let data = {managerId:userId,deviceId:deviceId,type:type,netWorking:netWorking};
this.$http.post('/deviceBinding/relieveBinding',data).then((result) => {
let data = result.data;
if(data.successful && (data.resultCode.code=='SUCCESS')){
window.location.reload(true);
}
});
},
}
}



